import axios from "axios";
import config from '../config.js';
import authHeader from './auth-header';

const baseUrl = config.BaseUrl + '/api/v1/users';

class UserService {
    get() {
        return axios.get(baseUrl, { headers: authHeader() });
    }

    getById(id) {
        const idUrl = `${baseUrl}/${id}`;

        return axios.get(idUrl, { headers: authHeader() });
    }

    getPaging(pageCount, pageSize) {
        const pagingUrl = `${baseUrl}/${pageCount}/${pageSize}`;
        return axios.get(pagingUrl, { headers: authHeader() });
    }

    getCount() {
        const countUrl = `${baseUrl}/count`;

        return axios.get(countUrl, { headers: authHeader() });
    }

    post(user) {
        return axios.post(baseUrl, user, { headers: authHeader() });
    }

    put(user) {
        return axios.put(baseUrl, user, { headers: authHeader() });
    }

    delete(id) {
        const idUrl = `${baseUrl}/${id}`;
        return axios.delete(idUrl, { headers: authHeader() });
    }
}

export default new UserService();

